import { computed } from 'vue'
import store from '@/store';


interface AnyObject {
    [key: string]: any;
}


/**
 * @description: // 返回响应式state
 * @param {Array} stateList state集合
 * @return {object}
 */
export const mapStoreState = (stateList: Array<string>): AnyObject => {
    const stateObjec = stateList.reduce((result: AnyObject, key: string) => {
        let itemKeys:Array<string> = key.split('/');
        let keyName:string = itemKeys.pop() || "";
        if (keyName) {
            result[keyName] = itemKeys[0] ? computed(() => store.state[itemKeys[0]][keyName]) : computed(() => store.state[keyName]);
        }
        return result;
    }, {})
    return stateObjec;
}

/**
 * @description: // 返回响应式actions
 * @param {Array} actsionList action集合
 * @return {object}
 */
export const mapStoreActions = (actsionList: Array<string>): AnyObject => {
    const actionsObj = actsionList.reduce((result: AnyObject, key: string) => {
        let actionName: string = key;
        let keyName: string | number = key.split('/').pop() || 0;
        if (keyName) result[keyName] = (data: any) => store.dispatch(actionName, data);
        return result;
    }, {})
    return actionsObj;
}

/**
 * @description: // 返回响应式mutations
 * @param {Array} mutationList mutaion集合
 * @return {object}
 */
export const mapStoreMutations = (mutationList: Array<string>): any => {
    const mutationsObj = mutationList.reduce((result: AnyObject, key: string) => {
        let mutaionName: string = key;
        let keyName: string | number = key.split('/').pop() || 0;
        if (keyName) result[keyName] = (data: any) => store.commit(mutaionName, data);
        return result;
    }, {})
    return mutationsObj;
}